.product {
  grid-column: 1/-1;

  @include on-tablet {
    grid-column: 2/-2;
  }

  @include on-desktop {
    grid-column: span 4;
  }

  &__photo {
    width: 100%;
  }

  &__title {
    margin-top: 24px;
  }

  &__description {
    margin-bottom: 16px;
    margin-top: 8px;
    line-height: 22.4px;
  }

  &__price {
    margin: 0;
    font-size: 24px;
    font-weight: 700;
    line-height: 33.6px;
  }

  &:hover {
    transform: scale(107%);
    z-index: 1;

    box-shadow: 1px 4px 20px 0 $text-color;

    transition:
    transform $effectDuration,
    box-shadow $effectDuration;
  }
}
